    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
ul li {
    list-style-type: none;
}
button:hover {
    cursor: pointer;
}

#app-container {
    display: flex;
    justify-content: space-between;
    min-height: 100vh;
}
#project-container {
    display: flex;
    flex-direction: column;
    flex-basis: 20%;
    max-width: 20%; 
    align-items: center;
}
#project-container > .title {
    flex-basis: 1.5em;
}
#project-list {
    width: 100%;
}
#project-list li {
    background-color: cyan;
    color: black;
    padding: 0.5em;
    margin: 0.1em;
    font-weight: 500;
    font-size: 1.1em;
    font-family: 'Courier New', Courier, monospace;
}
#project-list li:hover {
    background-color: lightgrey;
}
#task-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: flex-start;
    padding-left: 2em;
    background-color: rgba(242, 242, 242, 0.8);
}
#task-list {
    width: 100%;
    display: flex;
    flex-direction: column;
}
#task-list li {
    display: flex;
    align-items: center;
    width: 80%;
    padding: 1em;
    margin: 0.1em;
    font-size: 1.1em;
    border: 1px solid grey;
    border-radius: 5px;
}
.checked {
    background-color: lightgrey;
}
.task-completion-btn {
    justify-self: start;
    width: 1.5em;
    height: 1.5em;
    border: 1px solid grey;
    background-color: transparent;
}
.task-completion-btn:hover {
    background-color: lightgrey;
}
.task-completion-btn-checked {
    width: 1.5em;
    height: 1.5em;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-position: center;
    border: none;
}
.task-completion-btn-checked{
    background-color: transparent;
}
.task-text-checked {
    text-decoration: line-through;
}
.delete-btn {
    justify-self: end;
    width: 1.5em;
    height: 1.5em;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: cover;
    background-position: center;
    background-color: transparent;
    outline: none;
    border: none;
}
.delete-btn-checked {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

#task-list li p {
    flex-grow: 1;
    margin: 0 1em;
}
.text-btn {
    border: 1px solid black;
    background-color: transparent;
    padding: 1em;
    font-weight: bolder;
    font-family: 'cursive';
    margin: 0.5em 0;
    min-width: 10em;
}
.text-btn:hover {
    background-color: lightgray;
}
.title {
    text-align: center;
    margin: 0.5em 0;
}
.subtitle {
    font-size: 1.3em;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.7);
}
.title-container {
    display: flex;
    flex-basis: 1.5em;
    flex-direction: column;
    align-items: start;
    max-height: 5em;
}
.title-container-secondary {
    display: flex;
    align-items: end;
}
.title-container-secondary > .title {
    margin-top: 0.5em;
    margin-bottom: 0;
}
.options-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.options-btn {
    min-width: 3em;
    min-height: 3em;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.options-btn:hover {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    transform: scale(0.75); /* black png is bigger than grey png so appears larger, scaling down to make it same size */
}
.options-menu {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    position: absolute;
    left: 1em;
    top: 100%;
}
.options-menu button {
    padding: 1em 2em;
    font-weight: bolder;
    font-size: 1em;
    background-color: white;
    outline: none;
    border: none;
}
.options-menu button:hover {
    background-color: lightgrey;
}
.hide {
    display: none;
}
